.Projects{
padding: 20vh 0px;
margin: auto;
/* border: 1px solid red; */
height: auto;
width: 76vw;
}

.project-div-1{
    /* border: 1px solid red; */
    padding: 20px 0px;
}
.project-dis-div-2{
    /* border: 1px solid red; */
   
    padding: 20px 0px;
}
.project-img{
    height: 300px;
    width: 575px;
    object-fit: contain;
    mix-blend-mode: multiply;
}
.project-div-wrapper{
     /* border: 1px solid red; */
    display: flex;
    height: 300px;
    width: 1160px;
    gap: 10px;
}
button{
    background-color: transparent;
    width: 200px;
    height: 60px;
    cursor: pointer;
;
}
button:hover{
    background-color: rgba(0, 0, 0, 0.077);

    cursor: pointer;
;
}


.github{
    mix-blend-mode: multiply;
    height: 25px;
    width: 25px;
}

.Demo:hover{
    cursor: pointer;
}
 

/* .......................................................................................................................... */

@media screen and (max-width: 1080px) {
    .project-div-wrapper {
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
    
        /* width: 1160px; */
        gap: 10px;
        height: auto;
    }
}
@media screen and (max-width: 768px) {
    .project-div-wrapper {
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
     
        width: 375px;
        gap: 10px;
        height: auto;
    }
    .project-div-img .project-img{
        /* border: 1px solid red; */
        width: 375px;
        height: 300px;
        object-fit: contain;
    }
    button{
        width: 150px;
        height: 40px;
    }
}

@media screen and (max-width: 468px) {
    .project-div-wrapper {
        /* border: 1px solid red; */
        display: flex;
        flex-direction: column;
      
        width: 375px;
        gap: 10px;
        height: auto;
    }

    .project-img {
        height: 300px;
        width: 375px;
        object-fit: contain;
        mix-blend-mode: multiply;
}

  button{
     width: 150px;
}
}